export default {
  layout: "Profile-content",
  mounted() {
    this.getqualification();

  },
  data() {
    return {
      qualifications: '',
      qualification: {
          degree_name: "",
          subject: "",
          passing_year: "",
          institute_name: "",
      },
      errors: {},
    };
  },
  methods: {
    getqualification() {
      this.$axios
        .$get("/qualification/show")
        .then((res) => {
          console.log(res);
          this.qualifications = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    qualificationEdit(id) {
      $("#qualificationUpdate").modal("show");
      this.$axios
        .$get("/qualification/edit/" + id)
        .then((res) => {
          this.qualification = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    qualificationUpdate() {
      this.$axios
        .$post("/qualification/update/" + this.qualification.id, this.qualification)
        .then((res) => {
          this.getqualification();
          $("#qualificationUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";


        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    qualificationDelete(id) {
      if (confirm("Are you sure to delete this qualification?")) {
        this.$axios
          .$get("/qualification/delete/" + id)
          .then((res) => {
            this.getqualification();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    qualificationStatus(id) {
      this.$axios
        .$get("/qualification/status/" + id)
        .then((res) => {
          console.log(res);
          this.getqualification();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },





  },
};
